/*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*/

div#search-bar {
  width: calc(100% - 82px);
  padding-left: 82px;
  z-index: 97;
  position: fixed;
  background-color: rgb(48, 48, 48);
  -webkit-app-region: drag;
  #minimize {
    margin: 16px 8px 12px 16px;
    float: right;
    box-sizing: border-box;
    -webkit-app-region: no-drag;
  }
  #maximize {
    margin: 16px 8px 12px 8px;
    float: right;
    box-sizing: border-box;
    -webkit-app-region: no-drag;
  }
  #close {
    margin: 16px 16px 12px 8px;
    float: right;
    box-sizing: border-box;
    -webkit-app-region: no-drag;
  }
  #spacer {
    float: right;
    height: 30px;
    width: 1px;
    background-color: #808080;
    margin: 9px 8px 9px 8px;
    opacity: 0.5;
  }
  input[type=search] {
    -webkit-app-region: no-drag;
    float: right;
    height: 30px;
    width: 180px;
    padding-left: 10px;
    padding-right: 10px;
    margin: 9px 8px 9px 8px;
    border-radius: 4px;
    border: 1px solid rgb(34, 35, 38);
    background-color: white;//#808080;
    font-family: text;
    font-size: 20;
    transition: width 0.4s ease-in-out;
    &:focus {
      width: calc(100% - 175px);
    }
  }
}

/*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*/


#nav {
  height: 100%;
  width: 82px;
  background-color: rgb(34, 35, 38);
  z-index: 98;
  position: fixed;
  #app {
    margin: 21.5px 25px 21px 25px;
    float: left;
    box-sizing: border-box;
    -webkit-app-region: no-drag;
  }
  #browse {
    margin: 25px 25px 21px 25px;
    float: left;
    box-sizing: border-box;
    -webkit-app-region: no-drag;
  }
  #radio {
    margin: 25px 25px 21px 25px;
    float: left;
    box-sizing: border-box;
    -webkit-app-region: no-drag;
  }
  #library {
    margin: 25px 25px 21px 25px;
    float: left;
    box-sizing: border-box;
    -webkit-app-region: no-drag;
  }
  #oriz-spacer {
    width: 58px;
    height: 1px;
    background-color: #808080;
    margin: 0px;
    padding: 0px;
    margin-left: 12px;
    margin-right: 12px;
    float: left;
    box-sizing: border-box;
    -webkit-app-region: no-drag;
    opacity: 0.5;
  }
}

/*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*/

#main {
  height: 100%;
  width: 100%;
  z-index: 2;
  position: fixed;
  overflow-y: scroll;
  padding-left: 82px;
  padding-right: 82px;
}

/*~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*/

body {
  z-index: 1;
  position: relative;
  margin: 0px;
  background-image: linear-gradient(rgb(108, 53, 45), rgb(10, 5, 4) 85%);
  background-repeat: no-repeat;
}

html {
  z-index: 0;
  height: 100%;
  width: 100%;
}